import React, { useMemo, useRef, forwardRef, useImperativeHandle } from 'react';
import clsx from 'clsx';
import { PaddingDefaultProps, PaddingPropTypes } from './generated/propTypes';

const Padding = forwardRef((props, externalRef) => {
    const {
        tag,
        variant,
        children,
        className,
        customPadding,
        style,
        ...customProps
    } = props;

    const styles = useMemo(
        () => ({ ...style, padding: customPadding }),
        [style, customPadding]
    );

    const Tag = tag || 'div';

    const ref = useRef();

    useImperativeHandle(externalRef, () => ref.current, []);

    return (
        <Tag
            className={clsx(
                'lmnt',
                'lmnt-padding',
                variant === 'dense' && 'lmnt-padding--dense',
                variant === 'standard' && 'lmnt-padding--standard',
                variant === 'airy' && 'lmnt-padding--airy',
                className
            )}
            style={styles}
            {...customProps}
        >
            {children}
        </Tag>
    );
});

Padding.displayName = 'Padding';
Padding.propTypes = PaddingPropTypes;
Padding.defaultProps = PaddingDefaultProps;

export default Padding;
